import React from 'react';
import styles from '../styles/TransportDetailCard.module.css';

// 示例类型
export type TransportDetail = {
  from: string;
  to: string;
  code: string;
  plate: string;
  weight: string;
  status: string;
  depart: string;
  arrive?: string;
  materials: { code: string; name: string; qty: number }[];
  riskLevel: '高' | '中' | '低';
  progress: { label: string; time?: string; active?: boolean; done?: boolean; color?: string }[];
};

const mockDetail: TransportDetail = {
  from: 'COMAC浦东基地',
  to: 'COMAC大场基地',
  code: '010010',
  plate: '沪PEE3033',
  weight: '200kg',
  status: '运输中',
  depart: '14:00',
  arrive: '-',
  materials: [
    { code: '000000410000032320', name: '37框密封角盒G70', qty: 1 },
    { code: '00000041000081581', name: '连接片 5716C', qty: 2 },
  ],
  riskLevel: '中',
  progress: [
    { label: '出库装运', time: '2024/07/16 14:20:36', active: true, done: true, color: '#00ff66' },
    { label: '出库扫码', time: '2024/07/16 14:24:26', active: true, done: true, color: '#00ff66' },
    { label: '出库关门' },
    { label: '出卡' },
    { label: '进卡' },
    { label: '入库开门' },
    { label: '入库卸货' },
  ],
};

export default function TransportDetailCard({ detail = mockDetail }: { detail?: TransportDetail }) {
  return (
    <div className={styles.detailCard}>
      <div className={styles.header}>货物详情</div>
      <div className={styles.truckRow}>
        <img src="/truck.png" alt="truck" className={styles.truckImg} />
      </div>
      <div className={styles.routeTitle}>{detail.from} - {detail.to}</div>
      <div className={styles.infoRow}>
        <div>运输编号<br /><span>{detail.code}</span></div>
        <div>车牌<br /><span>{detail.plate}</span></div>
        <div>运输重量<br /><span>{detail.weight}</span></div>
        <div>状态<br /><span>{detail.status}</span></div>
        <div>实际出发<br /><span>{detail.depart}</span></div>
        <div>实际到达<br /><span>{detail.arrive || '-'}</span></div>
      </div>
      <hr className={styles.hr} />
      <div className={styles.sectionTitle}>当前状态</div>
      <div className={styles.progressRow}>
        {detail.progress.map((step, idx) => {
          const done = !!step.done;
          const nextDone = detail.progress[idx + 1]?.done;
          return (
            <div key={idx} className={styles.progressStep}>
              <div
                className={styles.progressDot}
                style={{
                  background: done ? (step.color || '#00ff66') : '#bbb',
                  boxShadow: done ? '0 0 12px #00ff66cc' : '0 0 6px #bbb8',
                  border: done ? '2px solid #00ff66' : '2px solid #bbb',
                }}
              ></div>
              <div
                className={styles.progressLabel}
                style={{ color: done ? (step.color || '#00ff66') : '#bbb' }}
              >
                {step.label}
              </div>
              {step.time && (
                <div className={styles.progressTime} style={{ color: done ? (step.color || '#00ff66') : '#bbb' }}>{step.time}</div>
              )}
              {idx < detail.progress.length - 1 && (
                <div
                  className={styles.progressLine}
                  style={{
                    background: done && nextDone ? '#00ff66' : '#bbb',
                    boxShadow: done && nextDone ? '0 0 8px #00ff6688' : 'none',
                  }}
                />
              )}
            </div>
          );
        })}
      </div>
      <div className={styles.sectionTitle} style={{marginTop:24}}>运输物料信息</div>
      <div className={styles.materialTable}>
        <div className={styles.materialHeader}>
          <div>物料编码</div>
          <div>名称</div>
          <div>数量</div>
        </div>
        {detail.materials.map((m, i) => (
          <div className={styles.materialRow} key={i}>
            <div>{m.code}</div>
            <div>{m.name}</div>
            <div>{m.qty}</div>
          </div>
        ))}
      </div>
      <div className={styles.sectionTitle} style={{marginTop:24,display:'flex',alignItems:'center',justifyContent:'space-between'}}>
        <span>货车安全风险预警等级</span>
        <span className={styles.riskLevel + ' ' + styles['risk' + detail.riskLevel]}>{detail.riskLevel}</span>
      </div>
    </div>
  );
} 